input[type=range] {
  height: 0;
  // -webkit-appearance: none;
  margin: 10px 0;
  width: 100%;
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 10px;
  cursor: pointer;
  // animate: 0.2s;
  box-shadow: 1px 1px 1px #a5a5a5;
  background: #3fa575;
  border-radius: 14px;
  border: 0px solid #000000;
}

input[type=range]::-webkit-slider-thumb {
  box-shadow: 0px 0px 1px #000000;
  border: 1.3px solid #072842;
  height: 23px;
  width: 40px;
  border-radius: 12px;
  background: #7eb6e4;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -3px;
}

input[type=range]:focus::-webkit-slider-runnable-track {
  background: #50555C;
}

input[type=range]::-moz-range-track {
  width: 100%;
  height: 10px;
  cursor: pointer;
  // animate: 0.2s;
  box-shadow: 1px 1px 1px #a5a5a5;
  background: #3fa575;
  border-radius: 14px;
  border: 0px solid #000000;
}

input[type=range]::-moz-range-thumb {
  box-shadow: 0px 0px 1px #000000;
  border: 1.3px solid #072842;
  height: 23px;
  width: 40px;
  border-radius: 12px;
  background: #7eb6e4;
  cursor: pointer;
}

input[type=range]::-ms-track {
  width: 100%;
  height: 14px;
  cursor: pointer;
  // animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

input[type=range]::-ms-fill-lower {
  background: #50555C;
  border: 0px solid #000000;
  border-radius: 28px;
  box-shadow: 1px 1px 1px #50555C;
}

input[type=range]::-ms-fill-upper {
  background: #50555C;
  border: 0px solid #000000;
  border-radius: 28px;
  box-shadow: 1px 1px 1px #50555C;
}

input[type=range]::-ms-thumb {
  margin-top: 1px;
  box-shadow: 0px 0px 0px #000000;
  border: 0px solid #000000;
  height: 23px;
  width: 40px;
  border-radius: 12px;
  background: #332068;
  cursor: pointer;
}

input[type=range]:focus::-ms-fill-lower {
  background: #50555C;
}

input[type=range]:focus::-ms-fill-upper {
  background: #50555C;
}